import { useState, useEffect, useCallback } from "react"
import { supabase } from "@/lib/supabase"

// ÁÆÄÂåñÁâà‰∫ëÁ´ØËøõÂ∫¶ HookÔºöÂè™ÁÆ°ÁêÜÂ∑≤ÂÆåÊàêÁöÑ lessonIdÔºàÊàñ courseIdÔºâ
export function useProgress() {
  const [completedLessons, setCompletedLessons] = useState<string[]>([])
  const [isLoading, setIsLoading] = useState(true)

  // ÂàùÂßãÂåñÔºö‰ªé Supabase ÊãâÂèñÂΩìÂâçÁî®Êà∑ÁöÑÂ∑≤ÂÆåÊàê lesson_id
  useEffect(() => {
    const load = async () => {
      try {
        const {
          data: { session },
        } = await supabase.auth.getSession()

        // Êú™ÁôªÂΩïÊó∂Áõ¥Êé•ÁªìÊùüÔºå‰∏çÊä•Èîô
        if (!session?.user) {
          setIsLoading(false)
          return
        }

        const { data, error } = await supabase
          .from("user_progress")
          .select("lesson_id")
          .eq("user_id", session.user.id)
          .eq("is_completed", true)

        if (error) {
          console.error("Failed to load user_progress:", error)
        } else if (data) {
          setCompletedLessons(data.map((row) => row.lesson_id))
        }
      } catch (err) {
        console.error("Failed to load progress:", err)
      } finally {
        setIsLoading(false)
      }
    }

    load()
  }, [])

  // ‰πêËßÇÊõ¥Êñ∞ + ÂêéÂè∞ upsert
  const markAsCompleted = useCallback(async (lessonId: string) => {
    // 1. Êú¨Âú∞‰πêËßÇÊõ¥Êñ∞
    setCompletedLessons((prev) => (prev.includes(lessonId) ? prev : [...prev, lessonId]))

    // 2. ÂêéÂè∞ÂêåÊ≠•Âà∞ Supabase
    try {
      const {
        data: { session },
      } = await supabase.auth.getSession()

      if (!session?.user) {
        // Êú™ÁôªÂΩïÔºåÁõ¥Êé•ËøîÂõûÔºå‰∏çÊä•Èîô
        return
      }

      await supabase.from("user_progress").upsert({
        user_id: session.user.id,
        course_id: lessonId, // ËøôÈáåÊöÇÁî®ËØæÁ®ã ID ‰Ωú‰∏∫ course_id
        lesson_id: lessonId,
        is_completed: true,
        updated_at: new Date().toISOString(),
      })
    } catch (err) {
      console.error("Failed to save progress:", err)
      // ÂèØ‰ª•Ê†πÊçÆÈúÄË¶ÅÂõûÊªöÊú¨Âú∞Áä∂ÊÄÅÔºåËøôÈáåÂÖà‰øùÊåÅ‰πêËßÇÊõ¥Êñ∞
    }
  }, [])

  const isCompleted = useCallback(
    (lessonId: string) => completedLessons.includes(lessonId),
    [completedLessons]
  )

  return {
    completedLessons,
    isLoading,
    markAsCompleted,
    isCompleted,
  }
}

